import Button from '@mui/material/Button';
import logo from '../../assets/logo.png'
import { Box } from '@mui/system';
import Stack from '@mui/material/Stack';
import img2 from '../../assets/imgs/304.png'
import img3 from '../../assets/imgs/305.png'
import car from '../../assets/imgs/carro.png'
import backgroundSemiFooter from '../../assets/imgs/backgroundHome.jpg'
import img1SemiFooter from '../../assets/imgs/autoserv1.jpg'
import img2SemiFooter from '../../assets/imgs/autoserv2.jpg'
import img3SemiFooter from '../../assets/imgs/autoserv3.jpg'
import img4SemiFooter from '../../assets/imgs/autoserv4.jpg'
import { Header, Menu, Card, CardHeader, CardBody, Box as Bx, Text } from 'grommet';
import { Link } from "react-router-dom";
import Polirimg from '../../assets/imgs/polimg.jpg'
import martelinhoimg from '../../assets/imgs/martelinhoservices.jpg'
import funilariaimg from '../../assets/imgs/funilariaimg.jpg'
import higenizacaoimg from '../../assets/imgs/higenizacaoimg.jpg'
import vidroimg from '../../assets/imgs/vidroimg.jpg'
import rodaimg from '../../assets/imgs/rodaimg.jpg'
const Services = () => {
    const middleButton = { textAlign: 'center', color: 'white', fontSize: 20 }
    const footerButton1 = { textAlign: 'center', color: 'white', fontSize: 40 }
    const footerButton2 = { textAlign: 'center', color: 'white', fontSize: 20 }
    const style = {
        navbarAtual: { backgroundColor: 'rgb(243 194 102)', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' },
        navbarSecundario: { backgroundColor: 'transparent', '&:hover': { backgroundColor: 'rgb(243 194 102)', opacity: [0.9, 0.8, 0.7], }, height: '5vh', fontFamily: 'Poppins' }
        // cardsServices: { }
    }
    return (
        <>
            <Header pad={{ left: 'xlarge', right: 'xlarge' }} background={{ color: 'rgb(179 179 179)' }}>
                <img src={logo} style={{ height: '10vh' }} />

                <Stack direction="row" mt={2} spacing={2} sx={{ position: 'relative' }}>
                    <Link to="/" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Home</Button>
                    </Link>
                    <Link to="/sobrenos" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Sobre ns</Button>
                    </Link>
                    <Button variant="contained" sx={style.navbarAtual}>Servios</Button>
                    <Link to="/contato" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Contato</Button>
                    </Link>
                    <Link to="/orcamento" style={{ textDecoration: 'none' }}>
                        <Button variant="contained" sx={style.navbarSecundario}>Fazer Oramento</Button>
                    </Link>
                </Stack>
            </Header>
            <Bx
                direction="column"
                border={
                    [
                        {
                            color: "transparent",
                            size: "30vh",
                            style: "solid",
                            side: "left"
                        },
                    ]}
                pad="medium"
            >
                {/* <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}> */}

                <h1 style={{ marginLeft: '32%' }}> NOSSOS SERVIOS</h1>
                {/* </Box> */}
            </Bx>

            <Bx gap='large'>
                <Stack direction="row" sx={{ width: '100%', justifyContent: 'center' }}>

                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={Polirimg} style={{ height: '100%', width: '50%' }} />
                        <Bx>
                            <h3> PINTURA, POLIMENTO E VITRIFICAO </h3>
                            <Text>
                                Para arranhes ou simplesmente embelezamento do seu carro. Aqui deixamos seu veculo novo, de novo!
                            </Text>
                        </Bx>
                    </Stack>

                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={funilariaimg} style={{ height: 'auto', width: '50%' }} />
                        <Bx>
                            <h3> FUNILARIA </h3>
                            <Text>
                                Pequenos ou grandes amassados, traz pra gente! Recuperamos ou trocamos o que for necessrio </Text>
                        </Bx>
                    </Stack>



                </Stack>

                <Stack direction="row" sx={{ width: '100%', height: '35vh', justifyContent: 'center' }}>

                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={rodaimg} style={{ height: '80%', width: '50%' }} />
                        <Bx>
                            <h3> PINTURA DE RODAS</h3>
                            <Text>
                                A pintura de roda a melhor opo para dar uma nova cara ao veculo
                            </Text>
                        </Bx>
                    </Stack>

                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={martelinhoimg} style={{ height: 'auto', width: '50%' }} />
                        <Bx >
                            <h3> MARTELINHO DE OURO </h3>
                            <Text>
                                Somos os mgicos do amassado tiramos aquelas mossas indesejadas
                            </Text>
                        </Bx>
                    </Stack>

                </Stack>

                <Stack direction="row" sx={{ width: '100%', justifyContent: 'center' }}>

                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={vidroimg} style={{ height: 'auto', width: '50%' }} />
                        <Bx >
                            <h3> RECUPERAO DE DELAMINAO DE VIDROS</h3>
                            <Text>
                                A delaminao ocorre devido ao tempo e  exposio dos vidros ao sol e chuva. As camadas se separam e formam bolhas no vidro. Aqui devolvemos seu vidro ao aspecto que ele deve ter
                            </Text>
                        </Bx>
                    </Stack>


                    <Stack maxWidth={600} sx={{ justifyContent: 'center', alignItems: 'center' }} direction="row" gap={2}>
                        <img src={higenizacaoimg} style={{ height: 'auto', width: '50%' }} />
                        <Bx >
                            <h3>HIGIENIZAO E HIDRATAO DE COURO
                            </h3>
                            <Text>
                                Revitalizamos o interior de seu carro, devolvendo aquele gostinho de carro zero    </Text>
                        </Bx>
                    </Stack>

                </Stack>
            </Bx>


            <Box style={{ justifyContent: 'center', alignItems: 'center', paddingTop: '2%', paddingBottom: '2%', backgroundColor: 'white' }}>
                <h1 style={{ textAlign: 'center' }}> FAA UM ORAMENTO
                </h1>
                <h1 style={{ color: 'rgb(243 194 102)', textAlign: 'center' }}> ORAMENTO SEM COMPROMISSO                    </h1>
                <Bx style={{ width: '20%', justifyContent: 'center', alignContent: 'center', display: 'flex', alignItems: 'center', marginLeft: '40%' }}>
                    <p style={{ color: 'black', textAlign: 'center' }}>
                        Para agilizar seu oramento, por favor preencha o formulrio abaixo e faa uploud das fotos de seu carro.

                        Se preferir nos mande um whatsapp  21  99139 3332 que fazemos o oramento por l.
                    </p>
                </Bx>
            </Box>






        </>
    );
}

export default Services;
